@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto"), url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Mono"), url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto Condensed"), url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock {
  overflow: hidden;
}

.hidden {
  display: none;
}

.btn {
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  display: block;
  padding: 15px 0;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
  border-radius: 3px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.05em;
  user-select: none;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn:hover {
  opacity: 50%;
}

.title {
  margin: 0;
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  color: #041827;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 769px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 35px 40px 20px;
  background-color: #00b2ff;
  border-radius: 3px;
}
@media (max-width: 769px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal__close-btn:focus {
  background-color: #000000;
  outline: 2px solid #2c39f2;
  border-radius: 10%;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 769px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal__title {
  font-family: "Roboto Condensed", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 42.19px;
  color: #ffffff;
  margin: 0 0 5px;
}
.modal__description {
  max-width: 420px;
  font-family: "PT Mono", "Courier New", "Courier", monospace;
  font-size: 16px;
  line-height: 21.5px;
  color: #ffffff;
  margin: 0 0 40px;
}
.modal__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 5px;
}
.modal__row {
  width: 100%;
  margin-bottom: 20px;
}
.modal__row input {
  width: inherit;
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 14px;
  font-family: "PT Mono", "Courier New", "Courier", monospace;
  font-size: 16px;
  line-height: 21.5px;
  color: #ffffff;
  border-radius: 3px;
  cursor: pointer;
}
.modal__row input::-webkit-input-placeholder {
  color: #ffffff;
}
.modal__row input:focus {
  outline: 2px solid #2c39f2;
  background-color: #000000;
}
.modal__row textarea {
  width: inherit;
  margin: 0;
  min-height: 155px;
  border: none;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 24px 15px;
  font-family: "PT Mono", "Courier New", "Courier", monospace;
  color: #ffffff;
  line-height: 21.5px;
  border-radius: 3px;
  cursor: pointer;
}
.modal__row textarea::-webkit-input-placeholder {
  color: #ffffff;
}
.modal__row textarea:focus {
  outline: 2px solid #2c39f2;
  background-color: #000000;
}
.modal__button {
  width: inherit;
  background-color: #041427;
  font-size: 16px;
  font-weight: 700;
  padding-bottom: 12px;
}
.modal__button:focus {
  outline: 3px solid #2c39f2;
  opacity: 90%;
}
.modal__row--agreement {
  display: flex;
  align-items: center;
}
.modal__row--agreement label {
  display: flex;
  flex-direction: row-reverse;
  font-family: "PT Mono", "Courier New", "Courier", monospace;
  font-size: 14px;
  line-height: 15.68px;
  color: #ffffff;
  opacity: 80%;
  cursor: pointer;
  margin-left: 10px;
}
.modal__row--agreement span {
  position: relative;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 1px;
  cursor: pointer;
  margin-right: 10px;
}
.modal .modal__row--agreement input[type=checkbox]:checked + span::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 4px;
  top: 35%;
  left: 25%;
  background-color: #ffffff;
  transform: rotate(145deg);
}
.modal .modal__row--agreement input[type=checkbox]:checked + span::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 6px;
  top: 25%;
  right: 40%;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.modal .modal__row--agreement input:focus-visible + span {
  background-color: #000000;
}

@media (max-width: 767px) {
  .accordion--closed .menu__list {
    display: none;
  }
  .accordion--closed .communication__list {
    display: none;
  }
  .accordion--oppened .menu__list {
    display: block;
  }
  .accordion--oppened .communication__list {
    display: block;
  }
  .accordion--oppened .menu__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
  .accordion--oppened .communication__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
  .accordion--closed .menu__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
  .accordion--closed .menu__button::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
    transform: rotate(90deg);
  }
  .accordion--closed .communication__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
  .accordion--closed .communication__button::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
    transform: rotate(90deg);
  }
  .accordion--nojs .menu__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
  .accordion--nojs .communication__button::after {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    top: 50%;
    right: 8%;
    background-color: #c4c4c4;
  }
}
